/**
  Переменные для отступов
 */

$offset-1: 8px;
$offset-2: 16px;
$offset-3: 24px;
$offset-4: 32px;
$offset-5: 40px;
$offset-6: 48px;

/**
  Переменные для цветов
 */
$primary: #12266b;
$accent: #1b389e;
$light: #edeff7;
$footer: #adbbe9;
$pattern: #273a81;

$white: #fff;
$black: #000;
$red: #eb4474;
$red2: #bc365d;
$green: #84bf75;
$green2: #6fbb5c;
$green3: #6a995e;

$gray0: #f7f7f7;
$gray1: #ccd3e5;
$gray2: #b2b9ce;
$gray3: #9599b1;
$gray-s: #dee6f0;
$gray-p: #f3f3f3;

/**
  Переменные для шрифтов
 */

$font-size-1: 12px;
$font-size-2: 16px;
$font-size-3: 18px;
$font-size-4: 24px;
$font-size-5: 36px;

%t-sm {
  font-weight: 500;
  font-size: 14px;
  line-height: 22px;
}

%t-normal {
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.02em;
}

%t-lg {
  font-size: 20px;
  line-height: 28px;
}

%header-sm {
  font-weight: 700;
  font-size: 23px;
  line-height: 28px;
}

%header-normal {
  font-weight: 800;
  font-size: 32px;
  line-height: 38px;
}

%header-lg {
  font-weight: 800;
  font-size: 52px;
  line-height: 60px;
}

%c-btn {
  font-weight: 500;
  font-size: 16px;
  line-height: 22px;
}

%price-cur {
  font-weight: 700;
  font-size: 20px;
  line-height: 26px;
}

%price-old {
  font-size: 18px;
  font-weight: 400;
  line-height: 26px;
}

%price-badge {
  font-size: 16px;
  font-weight: 600;
  line-height: 1;
}

%c-input {
  font-size: 16px;
  line-height: 22px;
}

%flex-center {
  display: flex;
  align-items: center;
  justify-content: center;
}

%flex-a-center {
  display: flex;
  align-items: center;
}

%flex-h-center {
  display: flex;
  justify-content: center;
}
