@include font-face('Montserrat', '/fonts/Montserrat-Regular', 400);
@include font-face('Montserrat', '/fonts/Montserrat-Medium', 500);
@include font-face('Montserrat', '/fonts/Montserrat-SemiBold', 600);
@include font-face('Montserrat', '/fonts/Montserrat-Bold', 700);
@include font-face('Montserrat', '/fonts/Montserrat-ExtraBold', 800);

* {
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
  -webkit-tap-highlight-color: transparent;
  color: $black;
  outline: none;
}

button {
  background-color: transparent;
  border: none;
  padding: 0;

  &:focus,
  &:active,
  &:hover {
    outline: none;
  }
}

.icon {
  &--24 {
    width: 24px;
    height: 24px;
  }

  &--16 {
    width: 16px;
    height: 16px;
  }
}

.btn {
  @extend %flex-center;
  @extend %c-btn;
  border-radius: 50px;
  padding: 0 40px;
  height: 48px;

  &--blue {
    background-color: $accent;
    color: $white;
    transition: 0.3s;

    &:hover {
      background-color: $primary;
      transition: 0.3s;
    }

    &:active {
      background-color: $pattern;
      transition: 0.3s;
    }

    span {
      color: $white;
    }
  }

  &--gray {
    background-color: $light;
    color: $accent;
    transition: 0.3s;

    &:focus,
    &:hover {
      background-color: $accent;
      color: $white;
      transition: 0.3s;
    }

    &:active {
      background-color: $pattern;
      transition: 0.3s;
    }
  }

  &--red {
    background-color: #f7edf0;
    color: $red;
    transition: 0.3s;

    &:focus,
    &:hover {
      color: $white;
      background-color: $red;
      transition: 0.3s;
    }

    &:active {
      background-color: $red2;
    }
  }

  &--green {
    background-color: $green2;
    color: $white;

    &:hover {
      color: $white;
      background-color: $green;
    }

    &:active {
      color: $white;
      background-color: $green3;
    }
  }

  &--white {
    background-color: $white;
    color: $accent;

    &:hover {
      background-color: $light;
    }

    &:active {
      background-color: $gray1;
    }
  }

  &--outline {
    border: 1px solid $light;
    color: $primary;

    &:hover,
    &:focus {
      background-color: $accent;
      color: $white;
    }

    &:active {
      background-color: $pattern;
    }
  }

  &--outline-carret {
    padding-right: 8px;

    &:after {
      display: block;
      margin-left: 18px;
      content: '';
      width: 24px;
      height: 24px;
      background-position: 50% 50%;
      background-repeat: no-repeat;
      background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M8.46968 18.5303C8.17677 18.2374 8.17677 17.7626 8.46968 17.4697L13.9393 12L8.46968 6.53031C8.17677 6.23741 8.17677 5.76261 8.46968 5.46971C8.76258 5.17681 9.23738 5.17681 9.53028 5.46971L15.5303 11.4697C15.8232 11.7626 15.8232 12.2374 15.5303 12.5303L9.53028 18.5303C9.23738 18.8232 8.76258 18.8232 8.46968 18.5303Z' fill='%2312266B'/%3E%3C/svg%3E");
    }

    &:hover,
    &:focus {
      &:after {
        background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M8.46968 18.5303C8.17677 18.2374 8.17677 17.7626 8.46968 17.4697L13.9393 12L8.46968 6.53031C8.17677 6.23741 8.17677 5.76261 8.46968 5.46971C8.76258 5.17681 9.23738 5.17681 9.53028 5.46971L15.5303 11.4697C15.8232 11.7626 15.8232 12.2374 15.5303 12.5303L9.53028 18.5303C9.23738 18.8232 8.76258 18.8232 8.46968 18.5303Z' fill='%23ffffff'/%3E%3C/svg%3E");
      }
    }
  }

  &--outline-red {
    background-color: transparent;
    border: 1px solid $red;
    color: $red;

    &:focus,
    &:hover {
      color: $white;
      background-color: $red;
      transition: 0.3s;
    }

    &:active {
      background-color: $red2;
    }
  }

  &--outline-dark {
    background-color: transparent;
    border: 1px solid $white;
    color: $white;
    transition: 0.3s;

    &:hover,
    &:focus {
      background-color: $white;
      color: $primary;
      transition: 0.3s;
    }

    &:active {
      background-color: $gray1;
      border-color: $gray1;
      transition: 0.3s;
    }
  }
}

a {
  color: inherit;
  transition: 0.3s;

  &:hover,
  &:focus,
  &:active {
    text-decoration: none;
    transition: 0.3s;
  }
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 40px;

  &--small {
    max-width: 1610px;
  }
}

p {
  margin-bottom: 0;
}

body {
  &.hidden {
    overflow: hidden;
  }
}

input[type='text'] {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  border-radius: 0;
}

input.input,
.input {
  @extend %c-btn;
  border: 1px solid $light;
  outline: none;
  height: 48px;
  border-radius: 50px;
  padding: 0 20px;
  color: $black;

  &::placeholder {
    color: $gray3;
  }

  &:hover {
    border-color: $gray2;
  }

  &:focus {
    border-color: $accent;
  }

  &.ierror {
    border-color: $red;
  }

  &.isuccess {
    border-color: $green;
  }
}

input[type='number']::-webkit-outer-spin-button,
input[type='number']::-webkit-inner-spin-button {
  -webkit-appearance: none; // Yeah, yeah everybody write about it
}

input[type='number'],
input[type='number']:hover,
input[type='number']:focus {
  appearance: none;
  -moz-appearance: textfield;
}

.position-relative {
  position: relative;
}

.caption {
  &--h2 {
    @extend %header-normal;
    color: $primary;
    margin-bottom: 20px;
    text-transform: uppercase;
  }

  &--h1 {
    @extend %header-lg;
    color: $primary;
    text-transform: uppercase;
  }

  &__description {
    @extend %t-normal;
    color: $primary;
    max-width: 600px;
    margin-bottom: 40px;
  }
}

.page {
  padding-bottom: 40px;
}

.header-top {
  background-color: $white;

  &__wrapper {
    @extend %flex-a-center;
    padding: 12px 0;
  }

  border-bottom: 1px solid $gray-s;
}

.header-logo {
  display: block;
  flex-shrink: 0;
  width: 166px;
  height: 68px;
  margin-right: 30px;

  svg {
    max-width: 100%;
    max-height: 100%;
  }
}

.header {
  z-index: 100;
  position: relative;

  &.fixed {
    min-height: 106px;

    .js-fx-header {
      position: fixed;
      top: 0;
      left: 0;
      right: 0;
      z-index: 100;
      background-color: $white;
      border-bottom: 1px solid $gray1;

      .header-logo {
        height: 56px;
        transition: 0.3s;
      }
    }

    .header-bottom-menu {
      position: fixed;
      left: 0;
      right: 0;
      top: 81px;
      z-index: 21;
    }
  }
}

.header-bottom {
  padding: 6px 0 6px;
  border-bottom: 1px solid $gray-s;
  min-height: 105px;
  background-color: $white;

  .js-fx-header {
    padding: 12px 40px;
  }

  &__wrapper {
    display: flex;
    align-items: center;
  }

  &__btn {
    margin-right: 60px;

    &--close {
      display: none;
    }

    &.active {
      .header-bottom__btn {
        &--open {
          display: none;
        }
        &--close {
          display: block;
        }
      }
    }

    svg {
      fill: #fff;
      margin-right: 10px;
    }
  }

  &__actions {
    display: flex;
    align-items: center;
    margin-left: auto;

    &-item {
      display: flex;
      align-items: center;
      margin-left: 30px;
      white-space: nowrap;

      &:first-child {
        margin-left: 0;
      }

      span {
        @extend %c-btn;
        color: $primary;
      }

      svg {
        fill: $primary;
      }

      &:hover,
      &:focus,
      &:active {
        span {
          color: $accent;
        }

        svg {
          fill: $accent;
        }
      }
    }
  }

  &__favorite {
    svg {
      margin-right: 5px;
    }
  }

  &__login {
    svg {
      margin-right: 5px;
    }
  }

  &__cart {
    &-icon {
      position: relative;
      margin-right: 10px;
    }

    &.active {
      .header-bottom__cart-icon {
        &:after {
          display: block;
          position: absolute;
          content: '';
          width: 10px;
          height: 10px;
          border-radius: 50%;
          background-color: $red;
          border: 2px solid $white;
          top: -1px;
          right: -2px;
        }
      }
    }
  }

  &__home {
    display: none;
  }

  &-mobile {
    margin-left: auto;
  }
}

.header-place {
  display: flex;
  margin-right: 20px;

  &__city {
    @extend %t-sm;
    color: $primary;
    margin-left: 10px;
  }

  &:hover,
  &:focus,
  &:active {
    color: $primary;
    text-decoration: underline;
  }
}

.header-contact {
  display: flex;
  align-items: center;

  &__number {
    @extend %t-sm;
    color: $primary;
    margin-left: 10px;
    white-space: nowrap;

    &:hover,
    &:active,
    &:focus {
      color: $primary;
      text-decoration: underline;
    }
  }

  &__callback {
    @extend %t-sm;
    color: $gray3;
    margin-left: 15px;

    &:hover,
    &:focus,
    &:active {
      text-decoration: underline;
    }
  }
}

.header-menu {
  display: flex;
  margin-left: auto;

  &__item {
    @extend %c-btn;
    color: $gray3;
    margin: 0 15px;

    &:hover,
    &:focus,
    &:active {
      color: $gray3;
      text-decoration: underline;
    }
  }
}

.header-overlay {
  display: none;
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  background-color: rgba($black, 0.3);
  z-index: -1;

  &.active {
    display: block;
  }

  &.r-active {
    display: block;
    z-index: 101;
  }
}

.header-bottom-menu {
  position: relative;
}

.header-search {
  position: relative;
  width: 500px;
  height: 24px;

  &:after {
    display: block;
    position: absolute;
    content: '';
    left: 0;
    right: 0;
    bottom: -14px;
    border-bottom: 1px solid transparent;
  }

  &.active {
    &:after {
      border-color: $accent;
    }

    .header-search {
      &__btn {
        svg {
          fill: $accent;
        }
      }

      &__clear {
        display: block;
      }
    }
  }

  &__form {
    display: flex;
    align-items: center;

    &:focus {
      border: 1px solid red;
    }
  }

  &__btn {
    margin-right: 8px;

    svg {
      fill: $gray2;
    }
  }

  &__clear {
    display: none;

    svg {
      fill: $gray2;
    }
  }

  &__field {
    @extend %c-input;
    border: none;
    width: calc(100% - 56px);
    max-width: 100%;

    &::placeholder {
      @extend %c-input;
      color: $gray3;
    }
  }
}

.header-search-mobile__btn {
  display: none;
}

.header-search-dropdown {
  display: none;
  position: absolute;
  background-color: $white;
  top: 64px;
  left: 0;
  right: 0;
  box-shadow: 0px 4px 50px rgba(0, 0, 0, 0.1);

  &.active {
    display: block;
  }

  &__all {
    display: flex;
    align-items: center;
    justify-content: center;
    border-top: 1px solid $gray-s;
    padding: 15px 10px 16px;

    span {
      @extend %t-sm;
      color: $accent;
      margin-right: 10px;
    }

    svg {
      fill: $accent;
    }

    &:focus,
    &:active,
    &:hover {
      span {
        color: $primary;
      }

      svg {
        fill: $primary;
      }
    }
  }

  &__item {
    display: flex;
    align-items: center;
    border-top: 1px solid $gray-s;
    padding: 21px 31px;
  }

  &__title {
    @extend %t-normal;
    color: $primary;
    width: calc(100% - 150px);
  }

  &__meta {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    width: 150px;
  }

  &__counter {
    @extend %price-old;
    color: $gray3;
  }

  &__price {
    &-wrapper {
      display: flex;
      padding-top: 5px;
    }

    &-current {
      @extend %price-cur;
      color: $red;
    }

    &-old {
      @extend %price-old;
      text-decoration: line-through;
      color: $gray3;
      margin-left: 15px;
    }
  }
}

.header.fixed .header-search-dropdown {
  top: 52px;
}

.header-social {
  display: grid;
  grid-template-columns: repeat(5, 24px);
  margin-left: 40px;
  gap: 24px;

  &__item {
    @extend %flex-center;
    width: 24px;
    height: 24px;

    svg path {
      fill: $gray2;
    }

    &:hover,
    &:focus,
    &:active {
      svg path {
        fill: $accent;
      }
    }
  }
}

.header-category {
  display: none;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  background-color: #fff;
  z-index: 99;
  grid-template-columns: 458px 1fr;
  border-bottom: 1px solid #ccd3e5;

  &.active {
    display: grid;
    background-color: $white;
  }
}

.header-category-menu {
  padding: 40px 20px 40px 40px;
  display: flex;
  flex-direction: column;
  border-right: 1px solid $gray1;

  &__item {
    display: flex;
    align-items: center;
    margin: 5px 0;

    &:hover,
    &:focus,
    &:active {
      background-color: #f5f9fc;
      color: $accent;
    }

    &.active {
      background-color: #f5f9fc;
      color: $accent;
    }

    &--special {
      a {
        color: $red !important;
      }
    }

    &-arrow {
      display: none;
      flex-shrink: 0;
      width: 24px;
      height: 24px;
      background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M8.46968 18.5303C8.17677 18.2374 8.17677 17.7626 8.46968 17.4697L13.9393 12L8.46968 6.53031C8.17677 6.23741 8.17677 5.76261 8.46968 5.46971C8.76258 5.17681 9.23738 5.17681 9.53028 5.46971L15.5303 11.4697C15.8232 11.7626 15.8232 12.2374 15.5303 12.5303L9.53028 18.5303C9.23738 18.8232 8.76258 18.8232 8.46968 18.5303Z' fill='%2312266B'/%3E%3C/svg%3E%0A");
    }
  }

  &__link {
    @extend %header-sm;
    color: $primary;
    display: block;
    padding: 14px 16px 14px 20px;
    width: 100%;
    text-transform: uppercase;
  }
}

.subcategory-menu {
  display: none;
  grid-template-columns: 2fr 3fr;

  &.active {
    display: grid;
  }

  &__menu {
    min-width: 230px;
    margin-right: 80px;
  }

  &__block {
    padding: 40px 60px 20px;
    border-right: 1px solid #ccd3e5;

    &-title {
      @extend %header-normal;
      color: $primary;
      display: inline-block;
      text-transform: uppercase;
      margin-bottom: 30px;

      &-wrapper {
        display: flex;
        align-items: center;

        span {
          display: none;
        }
      }
    }

    &-wrapper {
      display: flex;
    }
  }

  &__item {
    display: flex;
    align-items: center;
    position: relative;
    margin-bottom: 30px;

    .subcategory-to-btn {
      display: block;
      position: absolute;
      right: -50px;
      width: 24px;
      height: 24px;
      background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M8.46968 18.5303C8.17677 18.2374 8.17677 17.7626 8.46968 17.4697L13.9393 12L8.46968 6.53031C8.17677 6.23741 8.17677 5.76261 8.46968 5.46971C8.76258 5.17681 9.23738 5.17681 9.53028 5.46971L15.5303 11.4697C15.8232 11.7626 15.8232 12.2374 15.5303 12.5303L9.53028 18.5303C9.23738 18.8232 8.76258 18.8232 8.46968 18.5303Z' fill='%2312266B'/%3E%3C/svg%3E");
      background-position: 50% 50%;
      background-repeat: no-repeat;
    }

    &.active {
      a {
        color: $accent;
      }

      .subcategory-to-btn {
        background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M8.46968 18.5303C8.17677 18.2374 8.17677 17.7626 8.46968 17.4697L13.9393 12L8.46968 6.53031C8.17677 6.23741 8.17677 5.76261 8.46968 5.46971C8.76258 5.17681 9.23738 5.17681 9.53028 5.46971L15.5303 11.4697C15.8232 11.7626 15.8232 12.2374 15.5303 12.5303L9.53028 18.5303C9.23738 18.8232 8.76258 18.8232 8.46968 18.5303Z' fill='%231B389E'/%3E%3C/svg%3E");
      }
    }

    a {
      @extend %t-lg;
      font-weight: 500;
      color: rgba($primary, 0.5);

      &:hover,
      &:focus,
      &:active {
        color: $accent;
      }
    }
  }

  &__submenu {
    display: none;

    &.active {
      display: block;
    }

    &-title-wrapper {
      display: none;
    }

    &-item {
      margin-bottom: 30px;

      a {
        @extend %t-lg;
        font-weight: 500;
        color: rgba($primary, 0.5);

        &:hover,
        &:focus,
        &:active {
          color: $accent;
        }
      }
    }
  }
}

.header-category-extra {
  padding: 40px 40px 20px 60px;

  &__product-list {
    display: flex;
  }

  &__header {
    display: flex;
    align-items: flex-start;
    margin-bottom: 20px;

    .btn {
      position: relative;
      margin-left: auto;
      top: -6px;
    }

    .subcategory-menu__block-title {
      margin-bottom: 0;
    }
  }
}

.header-mobile-right {
  display: none;
}

.footer {
  background-color: $primary;
  background-image: url('/img/footer-bg.svg');

  &-top {
    border-bottom: 1px solid rgba($color: $white, $alpha: 0.1);
    &__wrapper {
      max-width: 1530px;
      margin: 0 auto;
      display: flex;
      padding: 30px 0;
    }

    &__contact {
      display: flex;
      flex-direction: column;
      &-number {
        @extend %header-normal;
        color: $white;
        margin-bottom: 20px;
      }

      &-btn {
        display: flex;
        margin-bottom: 20px;

        .btn:not(:last-child) {
          margin-right: 10px;
        }
      }
    }
  }

  &-menu {
    max-width: 260px;
    display: flex;
    margin-left: 115px;

    &__col {
      display: flex;
      flex-direction: column;
    }

    &__col:last-child {
      margin-left: auto;
    }

    &__link {
      @extend %t-sm;
      color: $white;

      &:hover,
      &:focus,
      &:active {
        color: $white;
        text-decoration: underline;
      }
    }

    &__link:not(:last-child) {
      margin-bottom: 15px;
    }
  }

  &-shop {
    margin-left: auto;

    &__title {
      @extend %t-sm;
      color: $white;
      margin-bottom: 15px;
    }

    &__list {
      display: flex;
      flex-direction: column;
    }

    &__item {
      margin-bottom: 15px;
      color: $footer;
      display: flex;
      align-items: center;

      &:hover,
      &:focus,
      &:active {
        color: $white;
        text-decoration: underline;
      }
    }

    &__item::before {
      display: block;
      content: '';
      width: 5px;
      height: 5px;
      background-color: $white;
      border-radius: 50%;
      margin-right: 10px;
    }

    &__all {
      @extend %t-sm;
      color: $white;

      &:hover,
      &:focus,
      &:active {
        color: $white;
        text-decoration: underline;
      }
    }
  }

  &-bottom {
    &__wrapper {
      max-width: 1530px;
      margin: 0 auto;
      display: flex;
      align-items: center;
      padding: 10px 0 20px;
