.extra-category {
  margin-bottom: 40px;

  &__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    border-top: 1px solid $gray-s;
    border-bottom: 1px solid $gray-s;

    &--add {
      display: grid;
      grid-template-columns: repeat(5, 1fr);
      grid-column: span 3;
      border-top: 1px solid $gray1;

      &-wrapper {
        display: none;
        grid-column: span 3;
      }

      .extra-category {
        &__item {
          height: 400px;
          grid-column: span 1;

          img {
            @include object-fit(cover);
            height: 100%;
            width: 100%;
            right: 0;
          }
        }

        &__decor {
          height: 300px;
        }
      }
    }
  }

  &__item {
    position: relative;
    padding: 40px;
    height: 660px;
    border-left: 1px solid $gray-s;

    &:first-child {
      border-left: none;
    }
  }

  &__link-wrapper {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }

  &__caption {
    @extend %header-sm;
    position: relative;
    z-index: 10;
    display: inline-block;
    color: $primary;
    margin-bottom: 34px;
    text-transform: uppercase;

    &:hover,
    &:focus,
    &:active {
      color: $accent;
    }
  }

  &__link {
    @extend %t-lg;
    color: $primary;
    margin-bottom: 28px;
  }

  &__decor {
    position: absolute;
    right: 0;
    z-index: -1;
    bottom: 0;
    overflow: hidden;
    max-width: 100%;
    max-height: 100%;
  }

  &__control {
    border-bottom: 1px solid $gray-s;
    display: flex;
    justify-content: center;
  }

  &__all {
    @extend %flex-center;
    @extend %c-btn;
    width: 100%;
    height: 54px;
    padding: 16px 0;
    color: $accent;

    span {
      padding-left: 10px;
    }

    &.active svg {
      transform: rotate(-180deg);
    }
  }

  &__special-list {
    padding: 40px 0 80px;

    &-wrapper {
      display: grid;
      grid-template-columns: repeat(6, 1fr);

      .extra-category {
        &__item {
          display: flex;
          flex-direction: column;
          border-left: none;
          height: auto;
          padding: 30px 23px;

          .btn {
            margin: 0 auto;
          }
        }

        &__caption {
          display: flex;
          text-align: center;
          align-items: center;
          justify-content: center;
          width: 100%;
          min-height: 56px;
          margin-bottom: 20px;
        }

        &__decor {
          position: static;
          width: 250px;
          height: 200px;
          margin: 0 auto 20px;
          z-index: 1;

          img {
            width: 100%;
            height: 100%;
            @include object-fit(cover);
          }
        }
      }
    }
  }
}

