  .container {
    padding: 40px;
  }

  &__block {
    max-width: 600px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .caption {
    &__description {
      margin-bottom: 10px;
    }
  }

  &__tag {
    margin-bottom: 30px;
    display: flex;

    &-item {
      padding: 9px 20px;
      background-color: $light;
      border-radius: 20px;
      @extend %c-btn;
      color: $accent;
      margin-right: 10px;

      &:hover,
      &:focus,
      &:active {
        color: $white;
        background-color: $accent;
      }
    }

    &-item:last-child {
      margin: 0;
    }
  }

  &__form {
    display: flex;
    margin-bottom: 15px;

    .input {
      width: 100%;
      max-width: 300px;
      margin-right: 10px;
    }
  }

  &__politic {
    max-width: 390px;
    @extend %t-sm;
    color: $gray2;
    margin: 0 auto;

    a {
      color: $accent;
      &:hover,
      &:focus,
      &:active {
        text-decoration: underline;
      }
    }
  }
}

.header-top__close-btn {
  display: none;
}

.go-to-head {
  display: none;
  position: sticky;
  z-index: 20;
  left: 50%;
  bottom: 40px;
  transform: translate3d(-50%, 0, 0);
  box-shadow: 0px 8px 20px rgba(27, 56, 158, 0.2);

  svg {
    margin-right: 10px;
  }
}

.js-category-mobile-btn {
  display: none;
}

.breadcrumbs {
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;

  &__item {
    @extend %c-btn;
    color: $gray3;
    margin-right: 6px;
    margin-bottom: 15px;

    &:after {
      content: '—';
      padding-left: 6px;
    }

    &:last-child {
      margin-right: 0;

      &:after {
        content: none;
      }
    }
  }
}

.product-page {
  .reviews-block {
    margin-bottom: 40px;
  }
}

.product-card {
  background-color: $gray0;
  background-image: url('/img/pattern.svg');
  padding: 40px 0;

  &__wrapper {
    display: grid;
    grid-template-columns: 50% 1fr;
    grid-template-rows: 72px 1fr;
    padding: 30px 0;
    background-color: $white;
  }

  .breadcrumbs {
    padding: 0 30px;
  }

  &__img {
    display: flex;
    padding: 0 30px;
    grid-row: span 2;
  }

  &__title {
    h1 {
      @extend %header-normal;
      color: $primary;
      margin: 0 0 10px 0;
      text-transform: uppercase;
    }
  }

  &__description {
    @extend %t-normal;
    color: $primary;
    margin-bottom: 20px;
    max-width: 400px;
  }

  &__content {
    margin-top: auto;
    padding: 0 30px;
  }

  &__label {
    @extend %price-badge;
    background-color: $red;
    color: $white;
    padding: 7px 10px;

    &-wrapper {
      display: flex;
      border-radius: 1px;
      margin-bottom: 10px;
    }
  }

  &__btn {
    &-wrapper {
      display: flex;
    }
  }

  &__buy {
    margin-right: 20px;
  }

  &__price {
    &-wrapper {
      display: flex;
      align-items: center;
      margin-bottom: 20px;
    }

    &-current {
      @extend %header-sm;
      color: $red;
      margin-right: 15px;
    }

    &-old {
      font-size: 16px;
      line-height: 28px;
      color: $gray3;
      text-decoration: line-through;
      margin-right: 40px;
    }
  }

  &__options {
    font-size: 16px;
    line-height: 28px;
    color: $gray3;
  }

  &__add-info {
    @extend %t-normal;
    color: $gray3;
    margin-bottom: 20px;
  }

  &__favorite {
    svg {
      margin-right: 10px;
      fill: $primary;
    }

    &:hover,
    &:focus,
    &:active {
      svg {
        fill: $white;
      }
    }
  }

  .product-slider-small {
    .slick-track {
      margin-top: -5px;
    }

    &__clip {
      height: 540px;
      width: 135px;
      overflow: hidden;
    }

    &__item {
      border: 2px solid transparent;

      &.slick-current {
        border-color: $primary;
      }

      &.slick-slide {
        margin: 5px 0;
        img {
          max-width: 100%;
        }
      }
    }
  }

  .product-slider-general {
    height: 100%;
    width: calc(100% - 165px);
    margin-left: auto;

    .slick-slide img {
      width: 100%;
      height: 100%;
      @include object-fit(cover);
    }
  }
}

.about-product {
  padding: 80px 0;

  &__wrapper {
    display: grid;
    grid-template-columns: 470px 1fr;
  }

  &__meta {
    margin-bottom: 30px;
    max-width: 290px;
  }

  .about-product {
    &--row {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 5px;

      &:last-child {
        margin-bottom: 0;
      }
    }

    &--space {
      margin-bottom: 30px;
    }

    &--gray {
      @extend %t-sm;
      color: $gray3;
    }
  }

  &__info {
    padding-right: 60px;

    span,
    p {
      @extend %t-normal;
      color: $primary;
    }
  }

  &__title {
    @extend %header-normal;
    text-transform: uppercase;
    color: $primary;
    margin-bottom: 20px;
  }

  &__img {
    width: 100%;
    height: 100%;
    position: relative;

    img {
      width: 100%;
      height: 100%;
      @include object-fit(cover);
    }

    &:after {
      display: block;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate3d(-50%, -50%, 0);
      content: '';
      width: 48px;
      height: 48px;
      background-image: url("data:image/svg+xml,%3Csvg width='48' height='48' viewBox='0 0 48 48' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Crect width='48' height='48' rx='24' fill='white'/%3E%3Cpath d='M22.4871 16.35L22.4373 16.318C21.8 15.9083 21.2513 15.5555 20.7953 15.3495C20.3251 15.1371 19.7388 14.9804 19.1614 15.2956C18.584 15.6109 18.3988 16.1888 18.3232 16.6991C18.2499 17.1941 18.25 17.8464 18.25 18.6041V18.6633V29.3367V29.3959C18.25 30.1535 18.2499 30.8059 18.3232 31.3008C18.3988 31.8112 18.584 32.3891 19.1614 32.7043C19.7388 33.0195 20.3251 32.8629 20.7953 32.6505C21.2513 32.4445 21.8 32.0917 22.4373 31.6819L22.4871 31.6499L30.7886 26.3132L30.8359 26.2828C31.3845 25.9302 31.8657 25.6209 32.2025 25.3264C32.5588 25.0148 32.9007 24.5948 32.9007 24C32.9007 23.4052 32.5588 22.9852 32.2025 22.6736C31.8658 22.3791 31.3847 22.0699 30.8362 21.7173L30.8359 21.7171L30.7886 21.6867L22.4871 16.35Z' fill='%2312266B'/%3E%3C/svg%3E");
      background-repeat: no-repeat;
      background-position: 50% 50%;
    }
  }
}

.product-recipe {
  padding-bottom: 80px;

  &__item {
    display: grid;
    grid-template-columns: 1fr 620px;
  }

  &__img {
    width: 100%;
    height: 100%;

    img {
      width: 100%;
      height: 100%;
      @include object-fit(cover);
    }
  }

  &__subtitle {
    @extend %t-sm;
    color: $gray3;
    margin-bottom: 10px;
  }

  &__title {
    @extend %header-sm;
    color: $primary;
    text-transform: uppercase;
    margin-bottom: 10px;
  }

  &__description {
    @extend %t-normal;
    color: $primary;
    margin-bottom: 40px;
  }

  &__time {
    @extend %c-btn;
    color: $primary;
    display: flex;
    align-items: center;

    svg {
      margin-right: 5px;
      position: relative;
      top: -1px;
    }

    &-wrapper {
      margin-bottom: 30px;
    }
  }

  &__btn {
    &-wrapper {
      display: flex;
    }

    &-more {
      margin-left: auto;
      padding-right: 7px;

      svg {
        margin-left: 18px;
        fill: $primary;
      }

      &:hover,
      &:focus,
      &:active {
        svg {
          fill: $white;
        }
      }
    }
  }

  &__tag {
    margin-top: auto;

    &-list {
      display: flex;
      flex-wrap: wrap;
      margin-bottom: 15px;
    }

    &-item {
      margin: 0 0 15px 15px;

      &:first-child {
        margin-left: 0;
      }
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    padding-left: 30px;
  }
}

.reviews-block {
  background-color: $gray0;
  background-image: url('/img/pattern.svg');
  overflow: hidden;
  padding: 60px 0;

  .caption--h2 {
    margin-bottom: 40px;
  }
}

.reviews-slider {
  width: 1840px;

  &.slick-slider {
    margin-bottom: 0;
    padding-bottom: 56px;
  }

  .slick-list {
    margin: 0 -10px;
  }

  .slick-slide {
    margin: 0 10px;
  }

  .slick-dots {
    display: flex;
    align-items: center;
    height: 48px;
    bottom: 0;
    width: auto;
    width: 220px;
    padding: 0 58px;
    left: 50%;
    margin-left: -265px;

    li {
      width: 100%;
      height: auto;
      margin: 0;
      padding: 0;

      &.slick-active {
        button {
          background-color: $primary;
        }
      }

      button {
        width: 100%;
        height: 1px;
        padding: 0;
        background-color: $gray1;

        &:before {
          content: none;
        }
      }
    }
  }

  .slick-arrow {
    display: block;
    z-index: 10;
    width: 48px;
    height: 48px;
    background-position: 50%;
    background-repeat: no-repeat;
    transform: none;
    top: auto;
    bottom: 0;
    right: auto;

    &:before {
      content: none;
    }

    &.slick-prev {
      background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M15.5302 5.4697C15.8231 5.76259 15.8231 6.23747 15.5302 6.53036L10.0606 12L15.5302 17.4697C15.8231 17.7626 15.8231 18.2374 15.5302 18.5303C15.2373 18.8232 14.7625 18.8232 14.4696 18.5303L8.46961 12.5303C8.17672 12.2374 8.17672 11.7626 8.46961 11.4697L14.4696 5.4697C14.7625 5.17681 15.2373 5.17681 15.5302 5.4697Z' fill='%2312266B'/%3E%3C/svg%3E%0A");
      right: calc(50% + 215px);
      left: auto;
    }

    &.slick-next {
      background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M8.46968 18.5303C8.17677 18.2374 8.17677 17.7626 8.46968 17.4697L13.9393 12L8.46968 6.53031C8.17677 6.23741 8.17677 5.76261 8.46968 5.46971C8.76258 5.17681 9.23738 5.17681 9.53028 5.46971L15.5303 11.4697C15.8232 11.7626 15.8232 12.2374 15.5303 12.5303L9.53028 18.5303C9.23738 18.8232 8.76258 18.8232 8.46968 18.5303Z' fill='%2312266B'/%3E%3C/svg%3E%0A");
      left: calc(50% - 95px);
    }
  }

  &__item {
    background-color: $white;
    padding: 30px;
  }

  &__name {
    @extend %t-normal;
    color: $primary;
    font-weight: 700;
    margin-bottom: 20px;
  }

  &__description {
    @extend %t-normal;
    color: $primary;
    position: relative;
    margin-bottom: 15px;
    height: 96px;
    overflow: hidden;

    &:after {
      position: absolute;
      content: '';
      bottom: 0;
      left: 0;
      height: 28px;
      width: 100%;
      background: linear-gradient(
        180deg,
        rgba(255, 255, 255, 0) 0%,
        #ffffff 100%
      );
    }
  }

  &__meta {
    display: flex;
    margin-top: auto;
  }

  &__show {
    @extend %t-normal;
    color: $accent;
  }

  &__date {
    @extend %t-normal;
    color: $gray2;
    margin-left: auto;
  }
}

.inner-header {
  background-color: $gray0;
  background-image: url('/img/pattern.svg');
  padding: 40px 0 10px;

  .breadcrumbs {
    margin-bottom: 25px;
  }

  &__caption {
    display: flex;
    align-items: baseline;
    margin-bottom: 20px;

    h1 {
      margin: 0 15px 0 0;
    }

    &-description {
      @extend %c-btn;
      color: $gray3;
    }
  }

  &__tag {
    &-list {
      display: flex;
      flex-wrap: wrap;
    }

    &-item {
      @extend %c-btn;
      display: flex;
      margin-right: 40px;
      margin-bottom: 20px;

      &:last-child {
        margin-right: 0;
      }

      &:hover,
      &:active {
        span {
          &:first-child {
            color: $accent;
          }
        }
      }

      span {
        color: $gray3;

        &:first-child {
          color: $primary;
          padding-right: 10px;
        }
      }

      &--special {
        span {
          &:first-child {
            color: $red;
          }
        }

        &:hover,
        &:active {
          span {
            &:first-child {
              color: $red;
            }
          }
        }
      }
    }
  }

  &__after-line {
  }

  &__clear-cart {
    @extend %c-input;
    color: $gray2;

    &:hover,
    &:active {
      color: $gray2;
      text-decoration: underline;
    }
  }
}

.catalog {
  &__wrapper {
    display: flex;
    padding-top: 30px;
  }

  &__sidebar {
    padding-right: 15px;
    width: 368px;
  }

  &__content {
    width: calc(100% - 368px);
  }
}

.filter {
  &__item {
    &--range {
      width: 100%;
      position: relative;
      margin: 13px 0;

      &:before,
      &:after {
        position: absolute;
        display: block;
        content: '';
        height: 2px;
        width: 12px;
        background-color: $gray1;
        box-shadow: inset 0 1px 1px #f0f0f0, 0 3px 6px -5px #bbb;
        top: 0;
        left: 0;
      }

      &:after {
        right: 0;
        left: auto;
      }
    }

    &--label {
      display: flex;
      align-items: center;
      height: 48px;
      border: 1px solid #dee6f0;
      border-radius: 48px;
      padding: 7px 15px;
      margin-right: 15px;

      &:last-child {
        margin-right: 0;
      }

      span {
        @extend %c-input;
        color: $gray3;
      }

      .range-input {
        @extend %c-input;
        text-align: right;
        color: $primary;
        max-width: 120px;
        width: calc(100% - 21px);
      }
    }

    &--input {
      display: flex;
      max-width: 100%;
      margin: 17px 0 26px;

      input {
        @extend %c-btn;
        color: $primary;
      }
    }
  }

  &__btn {
    margin-left: auto;

    svg {
      transform: rotate(-180deg);
      fill: $gray2;
    }
  }

  &__label {
    @extend %header-sm;
    color: $primary;
    text-transform: uppercase;
  }

  &__name {
    display: flex;
    align-items: center;
    padding: 14px 0;

    &.active {
      .filter__btn svg {
        transform: none;
      }
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding-top: 14px;

    &-link {
      @extend %t-lg;
      color: $primary;
      margin-bottom: 28px;
    }

    .range-input {
      border: none;
    }

    &-checkbox {
      margin-bottom: 28px;
      input {
        display: none;
      }

      span {
        @extend %t-lg;
        display: flex;
        align-items: center;
        color: $primary;

        &:before {
          display: block;
          content: '';
          width: 16px;
          height: 16px;
          border: 1px solid #b2b9ce;
          border-radius: 2px;
          margin-right: 10px;
        }
      }

      input:checked + span:before {
        background-color: $accent;
        background-position: 50% 50%;
        background-repeat: no-repeat;
        background-image: url("data:image/svg+xml,%3Csvg width='12' height='10' viewBox='0 0 12 10' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M11.6715 0.631428C11.8751 0.818022 11.8889 1.13431 11.7023 1.33787L4.3689 9.33787C4.27675 9.4384 4.14753 9.49693 4.01118 9.49987C3.87484 9.50287 3.7432 9.45 3.64677 9.35353L0.313437 6.0202C0.118177 5.82493 0.118177 5.5084 0.313437 5.31313C0.508697 5.11787 0.825284 5.11787 1.02054 5.31313L3.98462 8.2772L10.9651 0.662141C11.1517 0.458581 11.468 0.444828 11.6715 0.631428Z' fill='white'/%3E%3C/svg%3E");
      }
    }
  }
}

.price-range-slider {
  width: calc(100% - 24px);
  margin: 0 12px;

  &.noUi-horizontal {
    height: 2px;
    border: none;
    background-color: $gray1;

    .noUi-connect {
      background-color: $primary;
    }

    .noUi-handle {
      width: 24px;
      height: 24px;
      border-radius: 50%;
      top: -12px;
      right: -12px;
      box-shadow: none;
      border: 2px solid $primary;

      &:before,
      &:after {
        content: none;
      }
    }
  }
}

.sort-block {
  margin-bottom: 30px;
}

.mobile-btn-filter {
  display: none;
}

.sort-tag {
  display: flex;
  height: 65px;
  overflow-x: auto;
  overflow-y: hidden;

  &__wrapper {
    margin-bottom: 30px;
    height: 48px;
    width: calc(100% + 40px);
    overflow: hidden;
  }

  &__item {
    display: flex;
    align-items: center;
    background-color: $light;
    border-radius: 50px;
    padding: 0 20px;
    height: 48px;
    margin-right: 10px;

    &--outline {
      background-color: transparent;
      border: 1px solid $light;

      .sort-tag__remove {
        background-color: $light;
      }
    }
  }

  &__title {
    @extend %c-btn;
    color: $accent;
    margin-right: 10px;
    white-space: nowrap;
  }

  &__remove {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 24px;
    height: 24px;
    border-radius: 50%;
    background-color: $white;
    cursor: pointer;

    svg {
      fill: $accent;
    }
  }
}

.sort-type {
  display: flex;
  height: 22px;
  overflow: hidden;

  &__title {
    @extend %c-btn;
    color: $gray3;
    margin-right: 20px;
  }

  &__label {
    margin-right: 20px;
    margin-bottom: 0;

    span {
      @extend %c-btn;
      color: $gray3;
      cursor: pointer;
      white-space: nowrap;
    }

    &.active,
    &:hover,
    &:active {
      span {
        color: $primary;
      }
    }

    input {
      display: none;

      &:checked + span {
        color: $primary;
      }
    }
  }

  &__wrapper {
    display: flex;
    overflow-y: hidden;
    overflow-x: auto;
    height: 37px;
  }
}

.catalog-list {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 40px 0;

  .bs-wide {
    grid-column: span 4;
    order: 2;
  }

  .product-item {
    order: 1;

    &:nth-of-type(n + 9) {
      order: 3;
    }
  }
}

.inner-footer {
  background-color: $gray0;
  background-image: url('/img/pattern.svg');
  padding: 64px 0;

  &__wrapper {
    display: grid;
    grid-template-columns: 505px 1fr 505px;

    .btn {
      margin-left: auto;
    }
  }
}

.catalog-page {
  max-width: 100%;

  .inner-footer {
    margin-bottom: -88px;

    h2 {
      span {
        color: inherit;
      }
    }

    &__description {
      @extend %t-normal;
      color: $primary;
    }
  }
}

.catalog-list-paginator {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 40px 0;

  &__counter {
    @extend %t-normal;
    color: $primary;
  }

  &__progress {
    position: absolute;
    height: 1px;
    left: 0;
    top: 0;
    background-color: $primary;
    width: 10px;

    &-wrapper {
      display: block;
      position: relative;
      margin: 15px 0 20px;
      width: 100px;
      height: 1px;
      background-color: $gray-s;
    }
  }
}

.cart-page {
  padding-bottom: 0;

  .inner-header {
    padding: 40px 0 30px;
  }
}

.cart {
  overflow: hidden;

  &__wrapper {
    display: flex;
    padding: 30px 0 0 40px;
  }

  &__content {
    width: calc(100% - 395px);
  }

  &__sidebar {
    width: 395px;
  }

  .scrollbar-track-y {
    width: 2px;
    background-color: $gray1;
  }

  .scrollbar-thumb {
    width: 2px;
    border-radius: 0;
    background-color: $gray2;
  }
}

.cart-product-list {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  height: 600px;
  padding-right: 14px;

  &__wrapper {
    .cart-content__btn-wrapper {
      display: none;
    }
  }

  &:after {
    display: block;
    content: '';
    grid-column: span 4;
    height: 20px;
  }

  .product-item {
    border-color: $gray1;
    border-left: none;

    &__delete-btn {
      position: absolute;
      width: 24px;
      height: 24px;
      right: 30px;
      top: 30px;
      z-index: 10;

      svg {
        fill: $gray2;
      }
    }

    &:hover,
    &:active,
    &.active {
      .product-item {
        &__control {
          display: flex;
        }
      }
    }

    &__control {
      display: flex;
      padding: 30px 30px 0 30px;
    }

    .product-item {
      &__counter-block {
        display: flex;
        margin: 0 0 0 auto;
      }

      &__cart-total {
        p:first-child {
          @extend %t-sm;
          color: $gray3;
        }

        &-price {
          @extend %price-cur;
          color: $primary;
        }
      }
    }

    &:nth-of-type(n + 5) {
      border-top: none;
    }

    &:first-child,
    &:nth-of-type(4n + 5) {
      border-left: 1px solid $gray1;
    }
  }
}

.cart-sidebar {
  padding: 0 30px 0 45px;

  &__price {
    @extend %header-normal;
    color: $primary;

    &-wrapper {
      margin-bottom: 20px;

      button {
        display: none;
      }
    }

    &-description {
      @extend %t-normal;
      color: $primary;
      margin-bottom: 5px;
    }
  }

  &__description {
    padding-top: 20px;
    @extend %t-sm;
    color: $gray2;
  }

  &__meta {
    border-top: 1px solid $gray-s;
    padding: 10px 0 10px;

    &-row {
      display: flex;
      justify-content: space-between;
      padding: 10px 0;

      span {
        @extend %t-normal;
        color: $primary;
      }
    }
  }
}
