@media screen and (max-width: 1500px) {
  .extra-category {
    max-width: 100%;

    &__item {
      img {
        position: relative;
        right: -40px;
      }
    }

    &__special-list {
      .extra-category {
        &__item {
          padding: 30px 10px;

          img {
            right: 0;
          }
        }
      }
    }
  }

  .bs-double {
    br {
      display: none;
    }

    &__item-img {
      height: 600px;
    }
  }

  .advantages {
    &__list {
      gap: 0;

      br {
        display: none;
      }
    }
  }

  .footer {
    &-menu {
      margin-left: auto;
    }
  }

  .product-item {
    &.active,
    &:hover,
    &:focus,
    &:active {
      border-color: transparent;
    }

    &.active {
      border-color: transparent;

      &:hover,
      &:focus,
      &:active {
        border-color: transparent;
      }
    }
  }

  .about-product {
    &__wrapper {
      grid-template-columns: 460px 1fr;
    }

    &__img {
      max-height: 510px;
    }
  }

  .product-recipe {
    &__item {
      grid-template-columns: 1fr 575px;
    }
  }

  .catalog {
    &__sidebar {
      width: 340px;
    }

    &__content {
      width: calc(100% - 340px);
    }
  }

  .catalog-list {
    grid-template-columns: repeat(3, 1fr);
    gap: 0;

    .bs-wide {
      grid-column: span 3;
      margin: 40px 0;

      &__title {
        font-size: 32px;
        line-height: 38px;
        font-weight: 800;
      }

      &__description {
        font-size: 20px;
        line-height: 28px;
      }

      &__info {
        max-width: 485px;
      }

      &__form {
        justify-content: flex-end;
        flex-wrap: wrap;
        max-width: 300px;

        &-input {
          margin: 0 0 10px 0;
        }
      }
    }

    &.disabled {
      .product-item {
        &:nth-of-type(n + 16) {
          display: none;
        }
      }
    }

    .product-item {
      order: 1;

      &:nth-of-type(n + 9) {
        order: 1;
      }

      &:nth-of-type(n + 10) {
        order: 3;
      }
    }
  }

  .cart-product-list {
    grid-template-columns: repeat(3, 1fr);

    &:after {
      grid-column: span 3;
    }

    .product-item {
      &:nth-of-type(n + 4) {
        border-top: none;
      }

      &:first-child,
      &:nth-of-type(4n + 5) {
        border-left: none;
      }

      &:first-child,
      &:nth-of-type(3n + 4) {
        border-left: 1px solid $gray1;
      }
    }
  }
}

