    }
  }

  .catalog-list {
    grid-template-columns: 1fr 1fr;
    gap: 0 20px;

    .product-item {
      &:nth-of-type(n + 10) {
        order: 1;
      }

      &:nth-of-type(n + 7) {
        order: 3;
      }

      &:nth-of-type(n + 13) {
        display: none;
      }
    }

    .bs-wide {
      grid-column: span 2;
      height: 400px;

      &__info {
        padding-right: 0;
      }

      &__title {
        font-size: 32px;
        line-height: 38px;
        margin-bottom: 20px;
      }

      &__description {
        margin-bottom: 20px;
      }

      &__form {
        margin: 0;
        width: 100%;

        .btn {
          width: 100%;
        }
      }

      &__content {
        flex-wrap: wrap;
      }
    }
  }

  .catalog-list-paginator {
    width: 290px;
    max-width: 100%;
    margin: 0 auto;
    padding-bottom: 30px;
  }

  .sort-tag {
    padding-left: 16px;

    &__wrapper {
      width: calc(100% + 16px);
      margin-left: -16px;
      padding-left: 16px;
      height: 40px;
    }

    &__extra-wrapper {
      flex-wrap: wrap;
      width: calc(100% + 32px);
      margin: 0 -16px;

      .mobile-btn-filter {
        width: 100%;
        order: 1;
        margin: 20px 16px;
      }
    }

    &__title {
      font-size: 13px;
      line-height: 22px;
    }

    &__item {
      height: 40px;
      padding: 0 12px;
    }
  }

  .catalog-page {
    .inner-footer {
      &__wrapper {
        grid-template-columns: 1fr;
      }
    }
  }

  .filter {
    &__item {
      margin-bottom: 5px;

      &:last-child {
        margin: 0 0 5px 0;
      }

      &--input {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 20px;
        width: 100%;
        margin-top: -12px;
      }

      &--label {
        margin-bottom: 0;
      }
    }

    &__content {
      padding-top: 14px;

      &-link {
        font-size: 20px;
        line-height: 28px;
        margin-bottom: 28px;
      }

      &-checkbox {
        margin-bottom: 0;

        span {
          font-size: 20px;
          line-height: 28px;
          margin-bottom: 28px;
        }
      }
    }
  }

  .catalog-page {
    .breadcrumbs {
      padding: 0;
      align-items: flex-end;
      margin-bottom: 40px;

      &__item {
        margin-bottom: 0;
      }

      a.breadcrumbs__item {
        display: none;

        &:last-of-type {
          display: block;
          font-size: 0;
          width: 24px;
          height: 24px;
          background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M15.5302 5.4697C15.8231 5.76259 15.8231 6.23747 15.5302 6.53036L10.0606 12L15.5302 17.4697C15.8231 17.7626 15.8231 18.2374 15.5302 18.5303C15.2373 18.8232 14.7625 18.8232 14.4696 18.5303L8.46961 12.5303C8.17672 12.2374 8.17672 11.7626 8.46961 11.4697L14.4696 5.4697C14.7625 5.17681 15.2373 5.17681 15.5302 5.4697Z' fill='%239599b1'/%3E%3C/svg%3E%0A");
        }
      }
    }
  }

  .inner-header {
    h1 {
      font-size: 32px;
      line-height: 38px;
    }

    &__tag-item {
      margin-right: 15px;
    }
  }

  .inner-footer {
    &__wrapper {
      display: flex;
      flex-direction: column;
    }

    &__description {
      margin-bottom: 20px;
    }

    .btn {
      width: 100%;
    }
  }

  .catalog {
    &__wrapper {
      padding-top: 20px;
    }

    &__sidebar {
      flex-direction: column;
      width: calc(100% + 32px);
      margin-left: -16px;
    }
  }

  .product-counter {
    height: 48px;
    padding: 4px 8px;
  }

  .cart-sidebar {
    border-top: none;
    border-bottom: 1px solid $gray-s;
    margin-bottom: 20px;
    order: -1;
    padding-bottom: 10px;

    .cart-sidebar__btn {
      display: none;
    }

    &__mobile-toggle {
      display: none;
      margin-top: 20px;
    }

    &__price {
      &-wrapper {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 0;

        &.active {
          button {
            transform: rotate(180deg);
          }
        }

        button {
          display: block;
        }
      }
    }

    &__description {
      padding: 0 0 10px 0;
    }
  }

  .cart {
    overflow: visible;

    &__wrapper {
      padding: 0 8px 0 20px;
    }

    .cart-product-list {
      height: auto;
      grid-template-columns: 1fr 1fr;
      gap: 30px 20px;

      &__wrapper {
        position: relative;

        .cart-content__btn-wrapper {
          display: flex;
          position: sticky;
          align-items: center;
          justify-content: center;
          z-index: 10;
          left: 0;
          margin-left: -20px;
          width: calc(100% + 28px);
          bottom: 108px;
          background-color: $white;
          height: 69px;
          border-top: 1px solid $gray-s;
          padding: 0 20px;

          .cart-content__btn {
            width: 100%;
          }
        }
      }

      .product-item {
        border: none;

        &__control {
          padding: 30px 0 0 0;
        }

        &__delete {
          &-btn {
            top: 0;
            right: 0;
          }
        }
      }

      &:after {
        content: none;
        grid-column: span 2;
      }
    }
  }
}
